import React, { Component } from 'react';
import IdleTimer from 'react-idle-timer';

class UDTimeOut extends React.Component {

  // state is for keeping control state before or after changes.
  constructor(props) {
    super(props)
    this.state = {
      timeout: 60 * 1000 * this.props.minutes,
      isTimedOut: false
    }
    this.idleTimer = null
    this.onAction = this._onAction.bind(this)
    this.onActive = this._onActive.bind(this)
    this.onIdle = this._onIdle.bind(this)
  }

  componentWillMount() {
    this.pubSubToken = PubSub.subscribe(this.props.id, this.onIncomingEvent.bind(this));
  }

  componentWillUnmount() {
    PubSub.unsubscribe(this.pubSubToken);
  }

  onIncomingEvent(eventName, event) {
    if (event.type === "requestState") {
      var data = {
        attributes: {
          timeout: this.state.timeout,
          isTimedOut: this.state.isTimedOut,
        }
      }
      UniversalDashboard.post(`/api/internal/component/element/sessionState/${event.requestId}`, data);
    }
    else if (event.type === "setState") {
      this.setState(event.state.attributes);
    }
  }

  _onAction(e) {
    console.log('user did something', e);
    this.setState({ isTimedOut: false })
  }

  _onActive(e) {
    console.log('user is active', e);
    this.setState({ isTimedOut: false })
    //  console.log('time remaining', this.idleTimer.getRemainingTime())
  }

  _onIdle(e) {
    console.log('user is idle', e);
    this.setState({ isTimedOut: true })
    // console.log('last active', this.idleTimer.getLastActiveTime())
  }

  render() {
    return (
      <div>
        <IdleTimer
          ref={ref => { this.idleTimer = ref }}
          element={document}
          onActive={this.onActive}
          onIdle={this.onIdle}
          onAction={this.onAction}
          debounce={250}
          timeout={this.state.timeout} />
        {/* your app here */}
      </div>
    )

  }

}

export default UDTimeOut
